import {debounce} from "@/common/utils";
import BackTop from "@/components/content/backTop/BackTop";
// 定义混入方法 --  多个文件都可以使用这个代码。减少代码量
export const itemListenerMixin = {
  // 变量也可以进行混入，进行组合
  data(){
    return{
      aaa:'123'
    }
  },
  components:{

  },
  methods:{

  },
  mounted(){
    const refresh = debounce(this.$refs.scroll.refreshScroll, 100)
    this.homeImgLoadListener = () => {
      refresh();
    };
    this.$bus.$on('itemImgLoad', this.homeImgLoadListener)
    console.log('mixin中定义的mounted方法打印的！');
  }
}

export const backTopMixin = {
  components: {
    BackTop
  },
  data(){
    return{
      isShowBackTop:false
    }
  },
  methods:{
    backClick() {
      this.$refs.scroll.scroll.scrollTo(0, 0, 300);
    },
  }
}
