<template>
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <slot/>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'

export default {
  name: "Scroll",
  props: {
    probeType: {
      type: Number,
      default: 0,
    },
    pullUpLoad: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      scroll: null,
      message: 'scrollMessage',
    }
  },
  mounted() {
    this.scroll = new BScroll(this.$refs.wrapper, {
      click: true,
      probeType: this.probeType,
      // 使用滚动进行滑动时，必需开启的属性
      mouseWheel: true,
      pullUpLoad: this.pullUpLoad,
      // 2.xx版本有效，等到图片加载完后重新计算高度
      //observeDom: true,
      //observeImage: true
    })
    if (this.probeType === 2 || this.probeType === 3) {
      this.scroll.on('scroll', (position) => {
        //console.log(position);
        this.$emit('scroll', position)
        //影响性能，在home.vue中，监听数据变化，当list数据增加时，重新计算scroll高度
        //this.scroll.refresh();
      })
      this.scroll.on('pullingUp', () => {
        console.log('加载更多111');
        this.$emit('pullMore');
      })
    }

  },
  beforeDestroy() {
    this.$off('scroll');
  },
  //封装子组件的行为
  methods: {
    scrollTo(x, y, timeout = 300) {
      this.scroll && this.scroll.scrollTo && this.scroll.scrollTo(x, y, timeout)
    },
    finishPullUp() {
      this.scroll.finishPullUp();
    },
    refreshScroll() {
      //console.log('重新计算scroll');
      this.scroll && this.scroll.refresh();
    }
  }
}
</script>

